.Button {
  background-color: transparent;
  border: none;
  color: white;
  outline: solid 1px white;
  cursor: pointer;
  font: inherit;
  padding: 10px;
  margin: 30px;
  font-weight: bold;
}

.Success {
  color: #5c9210;
}

.Danger {
  color: #944317;
}



